import React, { Component } from 'react'
import NumberFormat from 'react-number-format';
import config from '../../constant/config';
import Data from '../Data';
import { Grid, Row, Col } from "react-bootstrap";
import './Statistics.css'
const axios = require('axios');
export default class Statistics extends Component {
    constructor(props) {
        super(props)
        this.state = {

            filterlist: [],
            selectedOption: null,
            allHistoryRecord: [],
            allOpenRecord: [],
            fromHistoryOrderInfo: [],
            toHistoryOrderInfo: [],
            commissionHistoryOrderInfo: [],
            fromOpenOrderInfo: [],
            dateTime:''
        }
    }
    componentDidMount() {
        this.fetchActivefilterData();
        this.fetchAllHIstoryTrade();
        this.fetchLastUpdatedTime()


    }
    fetchLastUpdatedTime = () => {
        let self = this;
        axios.get(config.baseurl + '/accounts/fetchLastUpdatedTime')
          .then(function (response) {
            if (response.status == 200) {
              self.setState({ dateTime: response.data.filteredInfoTime })
              // console.log(self.state.dataOpenTrade.accountFromInfo,"outyfjhkjh");
            }
          });
      }
    fetchActivefilterData = () => {
        let self = this;
        axios.get(config.baseurl + '/filter/fetchActivefilterdata')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ filterlist: response.data.rows })
                    // console.log(response.data.rows.accountFromInfo,"filterlist----------------------");

                    if (self.state.filterlist.length == 0) {
                        self.setState({ errorMassege: true })
                    }
                }
            });
    }

    showData = (accountDetailValue) => {

        return this.state.filterlist.length > 0 && this.state.filterlist.map((item, index) => {
            if (accountDetailValue === 'margin_level') {
                return (
                    <>
                        <td className="tdClr2 tdClr boldText" key={index}>
                            <NumberFormat value={item.accountFromInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} /> %
                </td>
                        <td className="tdClr2 boldText" key={index}>
                            {/* {item.accountToInfo[0].accounts_details[0][accountDetailValue]} % */}
                            <NumberFormat value={item.accountToInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} /> %
                </td>
                    </>
                )
            }
            if (item.accountFromInfo.length > 0) {
                return (
                    <>
                        <td className="tdClr2 tdClr boldText" key={index}>
                            {item.accountFromInfo[0].accounts_details[0].currency == 'USD' ? '$' : ''}  {' '}
                            <NumberFormat value={item.accountFromInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} />
                        </td>

                        <td className="tdClr2 boldText" key={index}>
                            {item.accountToInfo[0].accounts_details[0].currency == 'USD' ? '$' : ''} {' '}
                            {/* {item.accountToInfo[0].accounts_details[0][accountDetailValue]} */}
                            <NumberFormat value={item.accountToInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} />

                        </td>
                    </>
                )

            }

        })
    }
    tableHeader = () => {

        // return this.state.accountlist.length > 0 && this.state.accountlist.map((item, index) => {
        //   return (
        //     <th key={index} className={item.accounts_details[0].account_id == 1 && index !== (this.state.accountlist.length - 1) ? 'bText colorDarkBlue text-center tdClr2 tdClr' : 'bText colorDarkBlue text-center tdClr2'}>
        //       <span>
        //         {item.login}
        //       </span>
        //     <div className="colorDarkBlue" style={{fontSize:10}}> 
        //       ({(item.alias!=null) ? item.alias : '-'})
        //     </div>
        //     </th>

        //   )
        // }
        // )
        return this.state.filterlist.length > 0 && this.state.filterlist.map((item, index) => {
            return (
                <>
                    <th key={index} className="bText colorDarkBlue text-center tdClr2 tdClr">
                        <div className="colorDarkBlue" >
                            {(item.accountFromInfo[0].alias != null) ? item.accountFromInfo[0].alias : '-'}
                        </div>
                        <span style={{ fontSize: 10 }}>
                            {item.accountFromInfo[0].login}
                        </span>

                    </th>
                    <th key={index} className="bText color2 text-center tdClr2">
                        <div className="color2" >
                            {(item.accountToInfo[0].alias != null) ? item.accountToInfo[0].alias : '-'}
                        </div>
                        <span style={{ fontSize: 10 }}>
                            {item.accountToInfo[0].login}
                        </span>

                    </th>
                </>

            )

        })
    }
    fetchAllHIstoryTrade = () => {
        let self = this;
        axios.get(config.baseurl + '/accounts/fetchAllHistoryTrade')
            .then(function (response) {
                // console.log(response.data, 'response.data his')
                if (response.status == 200) {
                    // self.setState({ allHistoryRecord: response.data.rows })
                    self.setState({
                        allHistoryRecord: response.data.rows,
                        fromHistoryOrderInfo: response.data.fromHistoryOrderInfo,
                        toHistoryOrderInfo: response.data.toHistoryOrderInfo,
                        commissionHistoryOrderInfo: response.data.commissionHistoryOrderInfo
                    })
                }
            })
        // axios.post(config.baseurl + '/accounts/fetchAllBySymbolOpen', {'symbol':symbol})
        // axios.post(config.baseurl + '/accounts/fetchAllBySymbolOpen', postData)
        axios.get(config.baseurl + '/accounts/fetchAllOpenTrade')
            .then(function (response) {
                if (response.status == 200) {

                    // self.setState({ allOpenRecord: response.data.rows })
                    self.setState({
                        allOpenRecord: response.data.rows,
                        fromOpenOrderInfo: response.data.fromOpenOrderInfo,
                        toOpenOrderInfo: response.data.toOpenOrderInfo
                    })
                }
            })
    }
    render() {
        const { allHistoryRecord, allOpenRecord, selectedOption, toOpenOrderInfo, fromOpenOrderInfo, fromHistoryOrderInfo, commissionHistoryOrderInfo, toHistoryOrderInfo } = this.state;
        //   const sumOfOpenTotal = allOpenRecord.reduce((current, data) => {
        //     return (Math.floor(data.commission + data.swap + data.profit+ current));
        // }, 0);

        //   const sumOfHistoryTotal = allHistoryRecord.reduce((current, data) => {
        //     return (data.profit + current);
        // }, 0);
        let uniqueSymbols = "";
        if (allOpenRecord && Object.entries(allOpenRecord).length > 0) {
            let fromSymbols = JSON.parse(allOpenRecord.from_symbols);
            // fromSymbols = (fromSymbols).map(data => data).join(', ');
            let toSymbols = JSON.parse(allOpenRecord.to_symbols);
            let combineSymbols = fromSymbols.concat(toSymbols);
            uniqueSymbols = combineSymbols.filter((item, i, ar) => ar.indexOf(item) === i);
            uniqueSymbols = (uniqueSymbols).map(data => data).join(', ')
        }
        let fromsum = 0;
        let tosum = 0;
        // if(fromHistoryOrderInfo && commissionHistoryOrderInfo && fromHistoryOrderInfo.length>0){
        if (fromHistoryOrderInfo && fromHistoryOrderInfo.length > 0) {
            fromsum = (fromHistoryOrderInfo[0].commission + fromHistoryOrderInfo[0].swap + fromHistoryOrderInfo[0].profit);
        }

        if (toHistoryOrderInfo && toHistoryOrderInfo.length > 0) {
            tosum = (toHistoryOrderInfo[0].commission + toHistoryOrderInfo[0].swap + toHistoryOrderInfo[0].profit);
        }

        let sumOfHistoryTotal = tosum + fromsum;

        let openfromsum = 0;
        let opentosum = 0;
        if ((fromOpenOrderInfo && fromOpenOrderInfo.length > 0 && fromOpenOrderInfo[0].total != null)) {
            // if(allOpenRecord.swapFrominfo && allOpenRecord.swapFrominfo.length>0){
            //   openfromsum = (fromOpenOrderInfo[0].commission + allOpenRecord.swapFrominfo[0].open_value + fromOpenOrderInfo[0].profit + fromOpenOrderInfo[0].swap);
            // }else{
            openfromsum = (fromOpenOrderInfo[0].commission + fromOpenOrderInfo[0].swap + fromOpenOrderInfo[0].profit);

        }
        if ((toOpenOrderInfo && toOpenOrderInfo.length > 0 && toOpenOrderInfo[0].total != null)) {
            // if(allOpenRecord.swapToinfo && allOpenRecord.swapToinfo.length>0){
            //   opentosum = (toOpenOrderInfo[0].commission + allOpenRecord.swapToinfo[0].open_value + toOpenOrderInfo[0].profit+ toOpenOrderInfo[0].swap);
            // }

            // else{
            //   opentosum = (toOpenOrderInfo[0].commission + toOpenOrderInfo[0].swap + toOpenOrderInfo[0].profit);
            // }
            opentosum = (toOpenOrderInfo[0].commission + toOpenOrderInfo[0].swap + toOpenOrderInfo[0].profit);
        }

        let sumOfOpenTotal = opentosum + openfromsum;

        //   console.log(opentosum, 'opentosum')
        //   console.log(openfromsum, 'openfromsum')
        //  console.log(sumOfHistoryTotal, "sumOfHistoryTotal------");
        //  console.log(sumOfOpenTotal, "sumOfOpenTotal-----");
        //  console.log(allOpenRecord, 'allOpenRecord')



        return (
            <React.Fragment>
                <div className="statistic-wrapper">
                    <Grid fluid>
                        <Row>
                    <div className="lastUpdate">
                  <span> Last Updated :  </span>
                  {this.state.dateTime}(UTC)  </div>
                  </Row>
                        <div className="terminal-box">
                            {(this.state.errorMassege) && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Any Account</p>}
                            <span className="terminal-text">Terminal Statistics</span>
                        </div>
                        <Row noGutters={true}>
                            {Data.dashBoardPage.items.map((item, index) =>

                                <Col md={4} sm={6} key={index}>
                                    <div style={{ margin: '30px 0px' }} className="text-center">
                                        <span className="bText">{item.title}</span>
                                    </div>
                                    <div className="balance-box Pmargin"
                                    // className={item.id == '1' ? 'balance-box' : item.id == '2' ?
                                    //   'balance-box Pmargin' : item.id == '3' ? 'balance-box equity-box' :
                                    //     item.id == '4' ? 'balance-box Fmargin' : item.id == '5' ? 'balance-box floating' : 'balance-box  margin-box'}
                                    >
                                        <div className="content">
                                            <div className="table-responsive">

                                                <table className="table  text-center">

                                                    <tbody>
                                                        <tr>
                                                            {this.tableHeader()}
                                                        </tr>
                                                        <tr>
                                                            {item.title == 'Balance' && this.showData('balance')}
                                                            {item.title == 'Equity' && this.showData('equity')}
                                                            {item.title == 'Margin' && this.showData('margin')}
                                                            {item.title == 'Free Margin' && this.showData('free_margin')}
                                                            {item.title == 'Margin %' && this.showData('margin_level')}
                                                            {item.title == 'Floating P/L' && this.showData('profit')}
                                                        </tr>
                                                    </tbody>
                                                </table>

                                            </div>
                                        </div>
                                    </div>
                                </Col>

                            )}
                        </Row>
                        <Col md={12} sm={6}>
                            <div className={sumOfHistoryTotal + sumOfOpenTotal >= 0 ? "summary-box" : "summary-box-red"}>


                                {/* <span style={{fontWeight:'bold'}}>+150 USD</span> */}
                                {/* <p className="summary-text" style={{fontWeight:600}}>Summary For {(selectedOption!==null) && selectedOption.value} (open + closed) </p>
                <span style={{fontWeight:'bold'}}>{sumOfHistoryTotal + sumOfOpenTotal} $</span> */}

                                <Col md={12} sm={12}>
                                    <div className="summary-text text-center" style={{ fontWeight: 600, color: "#797979" }}>Summary For ({uniqueSymbols}) {allHistoryRecord.history_info !== 0 && (<span> + Commission</span>)} </div>
                                </Col>
                                <Col md={12} sm={12}>
                                    <div className="text-center" style={{ fontWeight: 'bold', fontSize: "18px" }}>$ {' '}
                                        <NumberFormat value={allHistoryRecord.history_info !== undefined ? sumOfHistoryTotal + sumOfOpenTotal + allHistoryRecord.history_info : sumOfHistoryTotal + sumOfOpenTotal} displayType={'text'} thousandSeparator={true} decimalScale={2} />


                                        {/* {sumOfHistoryTotal + sumOfOpenTotal} */}

                                    </div>
                                </Col>

                                <div className="clearfix"></div>
                            </div>
                        </Col>
                    </Grid>
                </div>
            </React.Fragment>
        )
    }
}
